@import "variables.less";
@import "elements.less";

@media only screen and (max-width: 6000px) and (min-width: 700px) {
  .wrapper.right-bar-enabled {
    .right-bar {
      right: 0;
      z-index: 99;
    }
  }
}

@media (min-width: 768px) and (max-width: 1024px) {

  .email-msg {
    display: none;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  body {
    overflow-x: hidden;
  }
}

@media (max-width: 767px) {
  body {
    overflow-x: hidden;
  }

  .enlarged {
    .left.side-menu {
      margin-left: -75px;
    }
  }
  .content-page {
    margin-left: 0px !important;
  }
  .footer {
    left: 0px !important;
  }

  .mobile-sidebar {
    left: 0px;
  }

  .mobile-content {
    left: 250px;
    right: -250px;
  }

  .wrapper-page {
    width: 90%;
  }

  .wizard > .steps > ul > li {
    width: 100%;
  }
  .wizard > .content {
    padding: 0px !important;
  }
  .wizard > .content > .body {
    float: none;
    position: relative;
    width: 100%;
    height: 100%;
    padding: 0%;
  }
  .wizard.vertical > .steps {
    display: inline;
    float: none;
    width: 100%;
  }
  .wizard.vertical > .content {
    display: inline;
    float: none;
    margin: 0%;
    width: 100%;
  }

  .navbar-nav {
    .open {
      .dropdown-menu {
        background-color: #ffffff;
        box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .26);
        left: auto;
        position: absolute;
        right: 0;
      }
    }
  }

  .todo-send {
    margin-top: 10px;
    padding-left: 15px;
  }

  .chat-inputbar {
    padding-left: 15px;
  }

  .chat-send {
    margin-top: 10px;
    padding-left: 15px;
    padding-right: 15px;
  }

  .dataTables_wrapper {
    .col-xs-6 {
      width: 100%;
      text-align: left;
    }
  }
}

@media (max-width: 480px) {
  .side-menu {
    z-index: 10 !important;
  }

  .button-menu-mobile {
    display: block;
  }

  .search-bar {
    display: none !important;
  }
}

@media (max-width: 420px) {
  .hide-phone {
    display: none !important;
  }
}

/* Container-alt */
@media (min-width: 768px) {
  .container-alt {
    width: 750px;
  }
}

@media (min-width: 992px) {
  .container-alt {
    width: 970px;
  }
}

@media (min-width: 1200px) {
  .container-alt {
    width: 1170px;
  }
}
@media (max-width: 450px) {
  .topbar-left {
    width: 70px !important;
  }
  .logo {
    .icon-c-logo {
      display: inline-block !important;
      line-height: 58px !important;
    }
    span {
      display: none !important;
    }
  }
  .content-page {
    margin-left: 70px;
  }
  .forced {
    .side-menu.left {
      box-shadow: 0 12px 12px rgba(0, 0, 0, 0.1);
    }
  }
  .enlarged {
    .side-menu.left {
      box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1) !important;
    }
  }

}

@media (max-width: 420px) {
  li.noti {
    position: static;
    ul.dropdown-menu {
      width: 405px !important;
      right: 5px !important;
    }
  }
}
@media (max-width: 375px) {
  li.noti {
    position: static;
    ul.dropdown-menu {
      width: 365px !important;
      right: 5px !important;
    }
  }
}
@media (max-width: 320px) {
  li.noti {
    position: static;
    ul.dropdown-menu {
      width: 310px !important;
      right: 5px !important;
    }
  }
}

/* Hub list res */

@media (min-width: 1440px) {
  .hubs-list > li {
    width: 12.5%;
  }
}

@media (min-width: 1024px) and (max-width: 1440px) {
  .hubs-list > li {
    width: 16.66%;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .hubs-list > li {
    width: 20%;
  }
}

@media (min-width: 425px) and (max-width: 768px) {
  .hubs-list > li {
    width: 25%;
  }
}

@media (min-width: 375px) and (max-width: 425px) {
  .hubs-list > li {
    width: 33.33%;
  }
}

@media (max-width: 375px) {
  .hubs-list > li {
    width: 50%;
  }
}
